@import url(https://fonts.googleapis.com/css2?family=Philosopher&family=Source+Serif+4:opsz@8..60&display=swap);
/* Google Fonts */

.App {
  text-align: left;
  background-color: #f8f7ff;
}

body {
  /* Without this, footer will be on top of bottom of page */
  padding-bottom: 50px;
  margin: 0;  
}

/* From Google Fonts import */
* {
  font-family: 'Philosopher', sans-serif;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.router {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  /* Needs this so all pages will have same height */
  min-height: 100vh;
}
a {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}
/* Change color of selected link in navbar */
.active {
    color: #201E50;
}
/* Color of non-selected link in navbar */
.not-active {
    color: rgba(255, 255, 255, 0.55);
}
/* All links in navbar have the styling below */
.not-active, .active {
    display: block;
    padding: 0.5rem 0;
    text-decoration: none;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}
.not-active:hover, .active:hover {
    color: #201E50;
}
/* No cursor to give the impression the link already selectec cannot be selected again */
.active:hover {
    cursor: none;
}




.header {
    background: #A36D90;
}
.footer {
    background: #201E50;
    padding: 15px;
    /* Needs this to garantee footer will be at the bottom of page and on entire width of it */
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
}

.icon {
    color: white;
    font-size: 25px;
}

.icon:hover {
    color:#D1345B ;
}
/* Makes image stay on the top left side of the page limiting its size*/
#image {
  width: auto;
  max-height: 230px;
  float: left;
  margin-right: 1rem;
}

p {
  font-size: 20px;
}
/* Images with same size and fill the entire card */
.image {
    object-fit:fill;
    height: 15rem;
    width: 100%;
}

/* Change image opacity when hovering over it */
.card:hover .image {
    -webkit-filter: opacity(0.15);
            filter: opacity(0.15)
}

/* Initial state of links */
.links {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
    align-items: center;
    opacity: 0;
    font-size: 30px;
    text-align: center;
}

/* Change links opacity to make them visible when hovering over the image */
.card:hover .links {
    opacity: 9;
}

.link {
    color: #0FA3B1;
    text-decoration: none;
}

.link:hover {
    color:#D1345B;
}

li {
    list-style-type: circle;
    font-size: 20px;
}

#download {
    font-size: 18px;
    padding-bottom: 5px;
    padding-left: 10px;
    border-bottom: 2px double #201E50;
    margin-bottom: 30px
}
